<template>
  <div>
      <van-picker
        title=""
        show-toolbar
        :columns="columns"
        :default-index="sex"
        @cancel="$emit('close')"
        @confirm="onConfirm"
        />
  </div>
</template>

<script>
import {editUserProfile} from '@/api/profile'

export default {
    data() {
        return {
            columns: ['', ''],
            sex : this.value    //
        };
    },
    methods: {
        async onConfirm (value, index) {
            this.$toast.loading({
                message : '...',
                forbidClick : true,   //
                duration : 0,        //
            })

            try {
                await editUserProfile({
                    gender : index
                })
                //
                this.$emit('input', index)
                //
                this.$emit('close')
                //
                this.$toast.success('')
            } catch (err) {
                this.$toast.fail('')
            }
        }
    },
    props: {
        value : {
            type : [Number, String],
            required : true
        }
    }
}
</script>

