<template>
    <div class="user-profile">
        <!--  -->
        <van-nav-bar
            @click-left="onClickLeft"
        >
        <div slot="title" style="color : #fff"></div>
        <van-icon name="arrow-left" slot="left" size="18px" />
        </van-nav-bar>

        <input type="file" hidden ref="file" @change="onFileChange">

        <!--  -->
        <van-cell class="Userphoto" title="" is-link @click="$refs.file.click()">
            <van-image
                class="avatar"
                fit="cover"
                round
                :src="UserProfiles.photo"
            />
        </van-cell>
        <van-cell title="" :value="UserProfiles.name" is-link @click="nickShow = true"/>
        <van-cell title="" :value="UserProfiles.gender === 0 ? '' : ''" is-link @click="sexShow = true"></van-cell>
        <van-cell title="" :value="UserProfiles.birthday" is-link @click="birthdayShow = true"></van-cell>

        <!--  -->
        <van-popup class="popup-wrap" v-model="nickShow" position="bottom" :style="{ height: '75%' }">
            <NickName v-if="nickShow" v-model="UserProfiles.name" @close="nickShow = false"></NickName>
        </van-popup>

        <!--  -->
        <van-popup v-model="sexShow" position="bottom" :style="{ height: '50%' }">
            <EditSex @close="sexShow = false" v-model="UserProfiles.gender"></EditSex>
        </van-popup>

        <!--  -->
        <van-popup v-model="birthdayShow" position="bottom" :style="{ height: '50%' }">
            <EditBirthday v-if="birthdayShow" @close="birthdayShow = false" v-model="UserProfiles.birthday"></EditBirthday>
        </van-popup>

        <!--  -->
        <van-popup v-model="photoShow" position="bottom" :style="{ height: '100%' }">
            <PreviewPhoto v-if="photoShow" :img="img" @close="photoShow = false" @updatePhoto="UserProfiles.photo = $event"></PreviewPhoto>
        </van-popup>
    </div>
</template>

<script>
import {getUserProfile} from '@/api/user'
import NickName from './components/nickname.vue'
import EditSex from './components/editSex.vue'
import EditBirthday from './components/editBirthday.vue'
import PreviewPhoto from './components/previewPhoto.vue'

export default {
    name : 'UserProfile',
    data () {
        return {
            UserProfiles : {},   //
            nickShow : false,      //
            sexShow : false,     //
            birthdayShow : false,   //
            photoShow : false,     //
            img : null,        //
        }
    },
    created () {
        this.UserProfile()
    },
    methods: {
        onClickLeft () {
            this.$router.back()
        },
        async UserProfile () {
            try {
                const {data} = await getUserProfile()
                this.UserProfiles = data.data
            } catch (err) {
                console.log('', err.message)
            }
        },
        onFileChange () {
            //
            const file = this.$refs.file.files[0]
            // blob 
            this.img = window.URL.createObjectURL(file)
            // console.log(data)
            this.photoShow = true

            //file-input  change 
            //value
            this.$refs.file.value = ""
        }
    },
    components: {
        NickName,
        EditSex,
        EditBirthday,
        PreviewPhoto
    }

}
</script>

<style lang="less" scoped>
.user-profile {
    .van-nav-bar {
        background-color: #3296fa;
        .van-nav-bar__title {
            color: #fff;
            letter-spacing: 2px;
        }
        .van-icon {
            color: #fff;
        }
    }

    .avatar {
        width: 60px;
        height: 60px;
    }

    .popup-wrap {
        background-color: #f5f5f9;
    }

    .Userphoto {
        padding-bottom: 0;
    }
}
</style> 