<template>
   <div>
       <van-nav-bar
            left-text=""
            right-text=""
            @click-left="onClickLeft"
            @click-right="onClickRight"
        >
        <div slot="title" style="color : #000; font-weight: normal;"></div>
        </van-nav-bar>

        <div class="field-wrap">
            <van-field
            v-model.trim="localName" 
            placeholder="" 
            maxlength = "7"
            show-word-limit
            />
        </div>   
   </div>
</template>

<script>
import {editUserProfile} from '@/api/profile'

export default {
    data () {
        return {
            localName : this.value
        }
    },
    props: {
        value : {
            type : String,
            required : true
        },
    },
    methods: {
        onClickLeft () {
            this.$emit('close')
        },
        async onClickRight () {
            this.$toast.loading({
                message : '...',
                forbidClick : true,   //
                duration : 0,        //
            })
            try {
                if (!this.localName.length) {
                    return this.$toast('')
                }
                await editUserProfile({
                    name : this.localName
                })
                // console.log(data)
                
                //
                this.$emit('input', this.localName)
                //
                this.$emit('close')
                //
                this.$toast.success('')
            } catch (err) {
                this.$toast.fail('')
            }
        }
    }
}
</script>

<style lang="less" scoped>
.field-wrap {
    padding: 16px;
}

</style>