<template>
  <div>
      <van-datetime-picker
        v-model="currentDate"
        type="date"
        show-toolbar
        title=""
        :min-date="minDate"
        :max-date="maxDate"
        @cancel="$emit('close')"
        @confirm="onConfirm"
        />
  </div>
</template>

<script>
import {editUserProfile} from '@/api/profile'
import dayjs, { Dayjs } from 'dayjs'

export default {
    props: {
        value : {
            type : String,
            required : true
        }
    },
    data() {
        return {
            minDate: new Date(1950, 0, 1),
            maxDate: new Date(),
            currentDate: new Date(this.value)
        };
    },
    methods: {
        async onConfirm() {
            this.$toast.loading({
                message : '...',
                forbidClick : true,   //
                duration : 0,        //
            })
            try {
                await editUserProfile({
                    birthday : dayjs(this.currentDate).format('YYYY-MM-DD')
                })
                //
                this.$emit('input', dayjs(this.currentDate).format('YYYY-MM-DD'))
                //
                this.$emit('close')
                //
                this.$toast.success('')
            } catch (err) {
                this.$toast.fail('')
            }
        }
    }
}
</script>

