<template>
  <div class="preview">
      <img :src="img" class="img" ref="img">

      <div class="toolbar">
          <div class="cancel" @click="$emit('close')"></div>
          <div class="confirm" @click="onConfirm"></div>
      </div>
  </div>
</template>

<script>
import 'cropperjs/dist/cropper.css'
import Cropper from 'cropperjs'
import {editUserPhoto} from '@/api/profile'

export default {
    props: {
        img : {
            type : [String, Object],
            required : true
        }
    },
    data () {
        return {
            cropper : null
        }
    },
    mounted () {
        const image = this.$refs.img
        this.cropper = new Cropper(image, {
            viewMode: 1,
            dragMode: 'move',
            aspectRatio: 1,
            // autoCropArea: 1,
            cropBoxMovable: false,
            cropBoxResizable: false,
            background: false,
            movable: true
        })
    },
    methods: {
        onConfirm () {
            // console.log(this.cropper.getCroppedCanvas())

            // getCroppedCanvas 
            this.cropper.getCroppedCanvas().toBlob(blob => {
                this.newUpated(blob)
            })
        },
        async newUpated (blob) {
            this.$toast.loading({
                message : '...',
                forbidClick : true,   //
                duration : 0,        //
            })
            try {
                // console.log(blob)
                //  Content-Type  multipart/form-data
                //  FormData 
                const formData = new FormData()
                formData.append('photo', blob)

                const {data} = await editUserPhoto (formData)
                //  Content-Type  application/json
                //  js

                //
                this.$emit('close')

                //
                this.$emit('updatePhoto', data.data.photo)

                //
                this.$toast.success('')
            } catch (err) {
                this.$toast.fail('')
            }
        }
    }
}
</script>

