.preview {
    background-color: #000;
    height: 100%;
    .img {
        max-width: 100%;
        display: block;
    }
    .toolbar {
        position: fixed;
        bottom: 0;
        left: 0;
        right: 0;
        display: flex;
        justify-content: space-between;
        color: #fff;
        .cancel {
            width: 90px;
            height: 90px;
            font-size: 32px;
            display: flex;
            justify-content: center;
            align-items: center;
            padding: 0 0 10px 20px;
        }
        .confirm {
            width: 90px;
            height: 90px;
            font-size: 32px;
            display: flex;
            justify-content: center;
            align-items: center;
            padding-right: 20px;
        }
    }
}
</style>